* {
    box-sizing: border-box;
}
html {
    scroll-behavior: smooth;
    font-size: 16px;
    @include for-tablet-layout {
        font-size: 12px;
    }
}
body {
    background-color: $darkBlue1;
    color: $White;
    font-family:'Open Sans';
    background-image: url('../../assets/images/bg-curvy-desktop.svg'), linear-gradient(#1c2431 8.8%, #181f2a 8%);
    background-repeat: no-repeat;
    background-position: center;
    background-position-y: 906px;
    @include for-tablet-layout {
        background-image: url('../../assets/images/bg-curvy-desktop.svg'), linear-gradient(#1c2431 1.9%, #181f2a 1.9%);
        background-position-y: 270px;
        background-size: contain;
    }
}
a {
    text-decoration: none;
    color: inherit;
    font-family:'Raleway';
    :focus {
        color: inherit;
    }
    :active {
        color: inherit;
    }    
}
p { 
    font-size: 1rem;
}
button {
    cursor: pointer;
    color: inherit;
    border: none;
}
input {
    outline: none;
    border: none;
}
h1, h2, h3, h4, h5, h6 { 
    font-family:'Raleway';
    font-weight: bold;
    text-align: center;
}
h1 { 
    font-size: 2.5rem;
    line-height: 60px;
}
h2 { 
    font-size: 1.25rem;
}
h3 {
    font-size: 0.625rem;
    letter-spacing: 0.5px;
    text-align: left;
    font-family:'Open Sans';
}